        display: flex;
        flex-direction: row;
        justify-content: space-between;
        font-weight: bold; }
    &__header {
        margin-top: 20px; }
    &__content {
        display: flex;
        position: relative;
        margin-top: 40px; }
    &__nav {
        padding-right: 60px; }
    &__first-elypse {
        position: absolute;
        z-index: -1;
        right: -80px;
        top: 30px; }
    &__second-elypse {
        position: absolute;
        right: -80px;
        top: 650px; } }


.nav-menu {
    margin-top: 20px;
    &__list {
        display: flex;
        flex-direction: row; }
    &__item {
        margin-right: 80px;
        font-size: 18px;
        font-weight: bold;
        a {
            text-decoration: none;
            color: black; } }


    &__item-last {
        font-size: 18px;
        a {
            text-decoration: none;
            color: black; } } }




.left {
