        background: $color-blue;
        border-radius: 100px;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-60deg); }

    &__rectangle-orange {
        position: absolute;
        width: 634.43px;
        height: 200px;
