.elypse {

    width: 220px;
    height: 220px;
    background: linear-gradient(132.05deg, rgba(252, 125, 77, 0.5) 14.92%, rgba(67, 143, 254, 0.5) 54.65%, rgba(59, 58, 61, 0.5) 85.56%);
