    display: flex;
    justify-content: center;
    align-items: center;
    &__substract {
        width: 152px;
        height: 152px;
        background: #E8E8E8;
        border-radius: 150px;
        &_big {
            width: 145px;
            height: 145px; }
        &_small {
            width: 98.91px;
            height: 98.91px;
            background: white;
            border-radius: 150px; } }
    &_big {
        width: 210px;
        height: 210px;
        transform: rotate(41.26deg); }
    &_small {
        width: 145px;
        height: 145px;
        transform: rotate(-75.17deg); }
    &_small2 {
        width: 145px;
        height: 145px;
        transform: rotate(51.23deg); }
    &_rot {
